import { TextField, Button, Box, Typography } from '@mui/material';
import React from 'react';
import { useState } from 'react';
// import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs'
// date-fns
// import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { AdapterMoment } from '@mui/x-date-pickers/AdapterMoment';

import { TimePicker } from '@mui/x-date-pickers/TimePicker';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import TimePick from './TimePick';

const REACT_APP_PATH_TO_SERVER = process.env.NODE_ENV === 'development'
  ? 'http://localhost:8080'
  : 'https://see-you-there.herokuapp.com';

const Form = ({ setTripState }) => {
  const defaultValues = {
    p1From: "",
    p2From: "",
    destination: "",
    p1DepTime: "",
  };
  const [formValues, setFormValues] = useState(defaultValues);
  const [timeValue, setTimeValue] = React.useState(null);
  const handleInputChange = (e) => {
    const {name, value} = e.target;
    setFormValues({...formValues, [name]: value});
  }

  const clearForm = e => {
    e.target.p1From.value = '';
    e.target.destination.value = '';
    e.target.p2From.value = '';
    e.target.p1DepTime.value = '';
  };
  const formSubmitHandler = async e => {
    e.preventDefault();
    console.log(formValues, timeValue);
    const formattedTime = timeValue._d.toLocaleTimeString('it-IT');
    console.log(formattedTime); 
    // const p1From = e.target.p1From.value;
    // const destination = e.target.destination.value;
    // const p2From = e.target.p2From.value;
    // const p1DepTime = e.target.p1DepTime.value;
    // if (p1From.length === 0
    //     || destination.length === 0
    //     || p2From.length === 0
    //     || p1DepTime.length === 0) {
    //   return;
    // }
    // clearForm(e);
    // // console.log(p1From, destination, p2From, p1DepTime);
    const uri = `${REACT_APP_PATH_TO_SERVER}/api/trips?from1=${formValues.p1From}&from2=${formValues.p2From}&destination=${formValues.destination}&departureTime=${formattedTime}`;
    const data = await fetch(uri);
    console.log(data, 'datagate');
    const parsedData = await data.json();
    console.log(parsedData);
    setTripState(parsedData);
    // window.scrollTo(0, 500);
  };

  return (
    <Box className="form-container container">
      <Typography variant="h3" component="h1" align="center" m={2} className="form-container__heading" >See you there!</Typography>
    <form onSubmit={formSubmitHandler} className="form">
      <TextField
        id="p1From-input"
        name="p1From"
        label="Where is person 1 travelling from?"
        type="text"
        value={formValues.p1From}
        onChange={handleInputChange}
        required
      />
      <TextField
        id="p2From-input"
        name="p2From"
        label="Where is person 2 travelling from?"
        type="text"
        value={formValues.p2From}
        onChange={handleInputChange}
        required
      />
      <TextField
        id="destination-input"
        name="destination"
        label="Where are you meeting?"
        type="text"
        value={formValues.destination}
        onChange={handleInputChange}
        required
      />
      <LocalizationProvider dateAdapter={AdapterMoment}>
        <TimePicker
          id="p1DepTime-input"
          label="When is person 1 leaving"
          name="p1DepTime"
          ampm={false}
          value={timeValue}
          onChange={(newValue) => {
          setTimeValue(newValue);
        }}
          renderInput={(params) => <TextField {...params} />}
        />
       </LocalizationProvider>
      <Button variant="contained" type="submit" color="primary">Submit</Button>
    </form>
    </Box>
  );
};

export default Form;
